.App {
  text-align: center;
  vertical-align: middle;
}

li {
  border-bottom: solid 1px rgb(158, 158, 158);
  text-align: left;
  padding: 10px 0;
}

.lstArtists {
  cursor: pointer;
}


.lstArtists:hover {
  color: #08b399;
}



li:first-child {
  border-top: solid 1px rgb(158, 158, 158);
  margin-top: 15px;
}

.box{
  margin-top: 30px;
}

.album {
  font-size: 36px;
  display: inline-block;
  width: 64px;
  padding: 15px;
  margin-right: 10px;
}

